<template>
    <div class="global-variable-panel">
        <div class="global-title">
            <span>{{i18n.global_varibles}}</span>
            <i
                class="bk-icon icon-info-circle global-variable-tootip"
                v-bk-tooltips="{
                    allowHtml: true,
                    content: '#var-desc',
                    placement: 'bottom-end',
                    duration: 0,
                    width: 400
                }">
            </i>
            <div id="var-desc">
                <div class="tips-item">
                    <h4>{{ i18n.attrTitle }}</h4>
                    <p>
                        {{ i18n.attrDesc1 }}
                        <i class="common-icon-show-left" style="color: #219f42"></i>
                        {{i18n.attrDesc2}}
                        <i class="common-icon-hide-right" style="color: #de9524"></i>
                        {{i18n.attrDesc3}}
                    </p>
                </div>
                <div class="tips-item">
                    <h4>{{ i18n.outputsTitle }}</h4>
                    <p>{{ i18n.outputsDesc }}</p>
                </div>
            </div>
        </div>
        <div class="add-variable">
            <bk-button theme="default" class="add-variable-btn" @click="onAddVariable">{{ i18n.new }}</bk-button>
        </div>
        <div class="global-variable-content">
            <div class="variable-header clearfix">
                <span class="col-name t-head">{{ i18n.name }}</span>
                <span class="col-key t-head">KEY</span>
                <span class="col-attributes t-head">{{ i18n.attributes }}</span>
                <span class="col-output t-head">{{ i18n.outputs }}</span>
                <span class="col-delete t-head"></span>
            </div>
            <ul class="variable-list" ref="variableList">
                <draggable class="variable-drag" v-model="constantsArray" :options="{ handle: '.col-item-drag' }" @end="onDragEnd">
                    <li
                        v-for="(constant, index) in constantsArray"
                        :key="constant.key"
                        :class="[
                            'clearfix',
                            'variable-item',
                            { 'variable-editing': isVariableEditing && theKeyOfEditing === constant.key }
                        ]">
                        <div class="variable-content" @click="onEditVariable(constant.key)">
                            <i class="col-item-drag bk-icon icon-sort"></i>
                            <span class="col-item col-name">
                                <p class="col-constant-name">{{constant.name}}</p>
                            </span>
                            <span class="col-item col-key">
                                <p class="col-constant-key">{{constant.key}}</p>
                                <a
                                    class="col-key-copy"
                                    href="javascript:void(0)"
                                    v-bk-tooltips.click="{
                                        content: i18n.copied,
                                        placements: ['bottom']
                                    }"
                                    @click.stop="onCopyKey(constant.key)">
                                    {{ i18n.copy }}
                                </a>
                            </span>
                            <span class="col-item col-attributes">
                                <span class="icon-wrap">
                                    <i
                                        v-if="constant.source_type !== 'component_outputs'"
                                        class="common-icon-show-left"
                                        v-bk-tooltips="{
                                            content: i18n.inputs,
                                            placements: ['bottom']
                                        }">
                                    </i>
                                    <i
                                        v-else
                                        class="common-icon-hide-right color-org"
                                        v-bk-tooltips="{
                                            content: i18n.outputs,
                                            placements: ['bottom']
                                        }">
                                    </i>
                                    <i
                                        v-if="constant.show_type === 'show'"
                                        class="common-icon-eye-show"
                                        v-bk-tooltips="{
                                            content: i18n.show,
                                            placements: ['bottom']
                                        }">
                                    </i>
                                    <i
                                        v-else
                                        class="common-icon-eye-hide color-org"
                                        v-bk-tooltips="{
                                            content: i18n.hide,
                                            placements: ['bottom']
                                        }">
                                    </i>
                                </span>
                            </span>
                            <span class="col-item col-output">
                                <div @click="onPreventDefalut">
                                    <bk-switcher
                                        size="small"
                                        on-text="ON"
                                        off-text="OFF"
                                        :selected="outputs.indexOf(constant.key) > -1"
                                        @change="onChangeVariableOutput(constant.key, $event)">
                                    </bk-switcher>
                                </div>
                            </span>
                            <i
                                class="col-item-delete common-icon-dark-circle-close"
                                @click.stop="onDeleteVariable(constant.key, index)">
                            </i>
                        </div>
                        <div
                            v-if="isVariableEditing && theKeyOfEditing === constant.key"
                            :key="`${constant.key}-edit`">
                            <VariableEdit
                                ref="editVariablePanel"
                                :variable-data="variableData"
                                :variable-type-list="variableTypeList"
                                :is-new-variable="false"
                                @scrollPanelToView="scrollPanelToView"
                                @onChangeEdit="onChangeEdit">
                            </VariableEdit>
                        </div>
                    </li>
                </draggable>
                <li v-if="isVariableEditing && theKeyOfEditing === ''">
                    <VariableEdit
                        ref="addVariablePanel"
                        :variable-data="variableData"
                        :variable-type-list="variableTypeList"
                        :is-new-variable="true"
                        @scrollPanelToView="scrollPanelToView"
                        @onChangeEdit="onChangeEdit">
                    </VariableEdit>
                </li>
                <li v-if="!isVariableEditing && !constantsArray.length" class="empty-variable-tip">
                    <NoData>
                        <p>{{i18n.emptyVariableTip}}</p>
                    </NoData>
                </li>
            </ul>
        </div>
        <bk-dialog
            width="400"
            ext-cls="common-dialog"
            :theme="'primary'"
            :mask-close="false"
            :header-position="'left'"
            :title="i18n.tips"
            :value="deleteConfirmDialogShow"
            @confirm="onConfirm"
            @cancel="onCancel">
            <div>{{ i18n.confirm }}</div>
        </bk-dialog>
    </div>
</template>

<script>
    import '@/utils/i18n.js'
    import { mapMutations, mapState } from 'vuex'
    import tools from '@/utils/tools.js'
    import draggable from 'vuedraggable'
    import VariableEdit from './VariableEdit.vue'
    import NoData from '@/components/common/base/NoData.vue'
    export default {
        name: 'TabGlobalVariables',
        components: {
            VariableEdit,
            draggable,
            NoData
        },
        props: ['isVariableEditing', 'variableTypeList'],
        data () {
            return {
                i18n: {
                    global_varibles: gettext(''),
                    new: gettext(''),
                    name: gettext(''),
                    attributes: gettext(''),
                    inputs: gettext(''),
                    outputs: gettext(''),
                    outputsTitle: gettext(''),
                    attr: gettext(''),
                    attrTitle: gettext(''),
                    attrDesc1: gettext('"/"'),
                    attrDesc2: gettext('/'),
                    attrDesc3: gettext('/'),
                    outputsDesc: gettext(''),
                    emptyVariableTip: gettext(''),
                    tips: gettext(''),
                    confirm: gettext(''),
                    copied: gettext(''),
                    copy: gettext(''),
                    show: gettext(''),
                    hide: gettext('')
                },
                copyText: '',
                theKeyOfEditing: '',
                constantsArray: [],
                deleteConfirmDialogShow: false
            }
        },
        computed: {
            ...mapState({
                'businessBaseInfo': state => state.template.businessBaseInfo,
                'outputs': state => state.template.outputs,
                'constants': state => state.template.constants,
                'timeout': state => state.template.time_out
            }),
            variableData () {
                if (this.theKeyOfEditing) {
                    return this.constants[this.theKeyOfEditing]
                } else {
                    return {
                        custom_type: 'input',
                        desc: '',
                        key: '',
                        name: '',
                        show_type: 'show',
                        source_info: {},
                        source_tag: 'input.input',
                        source_type: 'custom',
                        validation: '^.+$',
                        validator: [],
                        value: ''
                    }
                }
            }
        },
        watch: {
            constants: {
                handler () {
                    this.theKeyOfEditing = ''
                    this.constantsArray = this.getConstantsArray()
                    this.onChangeEdit(false)
                },
                deep: true
            }
        },
        created () {
            this.constantsArray = this.getConstantsArray()
        },
        methods: {
            ...mapMutations('template/', [
                'editVariable',
                'deleteVariable',
                'setOutputs',
                'setReceiversGroup',
                'setNotifyType',
                'setOvertime',
                'setCategory'
            ]),
            getConstantsArray () {
                const arrayList = []
                for (const cKey in this.constants) {
                    const constant = tools.deepClone(this.constants[cKey])
                    arrayList.push(constant)
                }
                const sortedList = arrayList.sort((a, b) => a.index - b.index)
                return sortedList
            },
            saveVariable () {
                if (this.theKeyOfEditing) {
                    return this.$refs.editVariablePanel[0].saveVariable()
                }

                return this.$refs.addVariablePanel.saveVariable()
            },
            scrollPanelToView (index) {
                if (index > 0) {
                    const itemHeight = document.querySelector('.variable-content').offsetHeight
                    this.$refs.variableList.scrollTop = itemHeight * index
                }
            },
            /**
             * 
             * @param {String} key key
             */
            onEditVariable (key) {
                if (key === this.theKeyOfEditing && this.isVariableEditing) {
                    this.onChangeEdit(false)
                } else {
                    this.onChangeEdit(true)
                    this.theKeyOfEditing = key
                }

                this.$emit('variableDataChanged')
            },
            /**
             * 
             */
            onDragEnd (event) {
                const { newIndex, oldIndex } = event
                const start = Math.min(newIndex, oldIndex)
                const end = Math.max(newIndex, oldIndex) + 1
                const indexChangedVariable = this.constantsArray.slice(start, end)
                indexChangedVariable.forEach((item, index) => {
                    item.index = index + start
                    this.editVariable({ key: item.key, variable: item })
                })
                this.$emit('variableDataChanged')
            },
            /**
             * 
             */
            onAddVariable () {
                this.onChangeEdit(true)
                this.theKeyOfEditing = ''
                this.$emit('variableDataChanged')
            },
            /**
             *  key 
             */
            onCopyKey (key) {
                this.copyText = key
                document.addEventListener('copy', this.copyHandler)
                document.execCommand('copy')
                document.removeEventListener('copy', this.copyHandler)
                this.copyText = ''
            },
            /**
             * 
             */
            copyHandler (e) {
                e.clipboardData.setData('text/html', this.copyText)
                e.clipboardData.setData('text/plain', this.copyText)
                e.preventDefault()
            },
            /**
             * 
             */
            onChangeVariableOutput (key, checked) {
                const changeType = checked ? 'add' : 'delete'
                this.setOutputs({ changeType, key })
                this.$emit('variableDataChanged')
            },
            /**
             *  
             */
            onDeleteVariable (key, index) {
                this.deleteVarKey = key
                this.deleteVarIndex = index
                this.deleteConfirmDialogShow = true
            },
            onConfirm () {
                const key = this.deleteVarKey
                const index = this.deleteVarIndex
                this.$emit('onDeleteConstant', key)
                const len = this.constantsArray.length
                if (len > 1) {
                    const indexChangedVariable = this.constantsArray.slice(index + 1, len)
                    indexChangedVariable.forEach((item, index) => {
                        item.index -= 1
                        this.editVariable({ key: item.key, variable: item })
                    })
                }
                this.deleteVariable(key)
                this.$emit('variableDataChanged')
                this.deleteConfirmDialogShow = false
            },
            onCancel () {
                this.deleteConfirmDialogShow = false
            },
            onChangeEdit (val) {
                this.$emit('changeVariableEditing', val)
            },
            onPreventDefalut (e) {
                window.event ? window.event.cancelBubble = true : e.stopPropagation()
            }
        }
    }
</script>

<style lang="scss" scoped>
@import '@/scss/config.scss';
@import '@/scss/mixins/scrollbar.scss';
$localBorderColor: #d8e2e7;
/deep/ .common-dialog .bk-dialog-body{
    padding: 20px;
}
.tips-item {
    & > h4 {
        margin: 0;
    }
    &:not(:last-child) {
        margin-bottom: 10px;
    }
}
.global-variable-panel {
    height: 100%;
    .global-title {
        height: 35px;
        margin: 20px;
        border-bottom: 1px solid #cacecb;
        span {
            font-size: 14px;
            font-weight:600;
            color:#313238;
        }
    }
    .add-variable {
        margin: 20px;
        .add-variable-btn {
            width: 90px;
        }
        .draft-form {
            display: inline-block;
            input {
                width: 200px;
            }
        }
    }
    .global-variable-tootip {
        vertical-align: middle;
        margin-left: 6px;
        color:#c4c6cc;
        cursor: pointer;
        &:hover {
            color:#f4aa1a;
        }
    }
    .global-variable-content {
        height: calc(100% - 120px);
        border-top: 1px solid $localBorderColor;
    }
    .variable-header, .variable-list {
        position: relative;
        font-size: 12px;
        .col-name {
            width: 100px;
        }
        .col-key {
            width: 128px;
        }
        .col-attributes {
            padding-left: 4px;
            width: 70px;
            .icon-wrap {
                vertical-align: middle;
                line-height: 1;
                display: inline-block;
                .common-icon-show-left {
                    color: #219f42;
                    font-size: 14px;
                }
                .common-icon-hide-right {
                    font-size: 14px;
                }
                .common-icon-eye-show {
                    margin-left: 8px;
                    color: #219f42;
                    font-size: 15px;
                }
                .common-icon-eye-hide {
                    margin-left: 8px;
                    font-size: 15px;
                }
                .color-org{
                    color: #de9524;
                }
            }
        }
        .col-output {
            width: 50px;
        }
    }
    .variable-header {
        padding: 0 20px 0 45px;
        background: #ecf0f4;
        border-bottom: 1px solid $localBorderColor;
        .t-head {
            float: left;
            height: 40px;
            line-height: 40px;
            font-size: 14px;
        }
    }
    .variable-list {
        width: 100%;
        height: calc(100% - 50px);
        overflow-x: hidden;
        overflow-y: auto;
        @include scrollbar;
        .variable-item {
            position: relative;
            cursor: pointer;
            &:hover {
                background: $blueStatus;
            }
            &.variable-editing {
                background: $blueStatus;
            }
            .variable-content {
                display: table;
                padding: 0 20px 0 45px;
                height: 40px;
                line-height: 40px;
                &:hover {
                    .col-item-drag {
                        display: inline-block;
                    }
                    .col-item-delete {
                        display: inline-block;
                    }
                }
                .col-item-delete {
                    color: #c4c6cc;
                    &:hover {
                        color: #979ba5;
                    }
                }
            }
        }
        .col-item {
            display: table-cell;
            font-size: 12px;
            vertical-align: middle;
            word-break: break-all;
            text-align: left;
            border-bottom: 1px solid #ebebeb;
        }
        .col-item-drag {
            display: none;
            position: absolute;
            top: 15px;
            left: 20px;
            color: #979ba5;
            cursor: move;
            &:hover {
                color: #348aff;
            }
        }
        .col-item-delete {
            display: none;
            position: absolute;
            top: 15px;
            right: 20px;
            font-size: 14px;
            color: #979ba5;
        }
        .col-name {
            .col-constant-name {
                width: 90px;
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
            }
        }
        .col-key {
            position: relative;
            .col-constant-key {
                display: inline-block;
                width: 90px;
                vertical-align: middle;
               line-height: 2;
            }
            .col-key-copy {
                margin-left: 2px;
                color: #52699d;
                text-decoration: underline;
            }
        }
        .col-output {
            .bk-switcher .bk-switcher-small {
                margin-left: 32px;
            }
            .bk-switcher.bk-switcher-small {
                width: 28px;
                height: 16px;
                line-height: 10px;
            }
            .bk-switcher.bk-switcher-small:after {
                top: 1px;
                width: 14px;
                height: 14px;
            }
            .bk-switcher.bk-switcher-small.is-checked:after {
                margin-left: -15px;
            }
        }
        .variable-edit-td {
            padding: 0;
            width: 412px;
        }
        .empty-variable-tip {
            margin-top: 120px;
        }
    }
}
.tooltip-content {
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
    h4 {
        margin-top: 0;
        margin-bottom: 10px;
    }
}
</style>
